/* eslint-disable */
import crypto from 'crypto';

export default {
    name: "Article",
    data: () => ({
        likeNum: 0,
        commentNum: 0,
        coverLoaded: false,
        avatarLoaded: true,
        animationState: false
    }),
    props: {
        post: Object,
        format: String
    },
    computed: {
        avatar: function() {
            return this.$store.state.global.themeConfig.avatar.url;
        },
        author: function() {
            return this.$store.state.global.hexoConfig.site.author;
        },
        lastCategory: function() {
            const len = this.post.categories.length;
            if (!len) {
                return '';
            } else {
                return this.post.categories[len - 1].name; // this.post.categories[len - 1].slug;
            }
        },
        disqusOptions: function () {
            return this.$store.state.global.themeConfig.disqus;
        },
        articleId: function () {
            let randSalt = (len = null) => {
                const salts = [
                    '(/ω＼)',
                    '٩(˃̶͈̀௰˂̶͈́)و',
                    '(^ω^)',
                    '(´▽｀)',
                    '⁄(⁄⁄⁄ω⁄⁄⁄)⁄',
                    'o(^▽^)o'
                ];
                len = len === null ? Math.round(Math.random() * salts.length) : len < 0 ? Math.ceil(len) : Math.floor(len);
                let index = Math.abs(len) % salts.length;
                return salts[index];
            }
            return crypto.createHash('md5').update(process.env.VUE_APP_SECRET  + '|' + (this.post.slug + this.post.title) + '|' + randSalt((this.post.slug + this.post.title).length)).digest('hex').substring(4, 29)
        },
    },
    created: async function() {
        let likeNum = await this.$axios.get(process.env.VUE_APP_AIM_API + '/Common/Counter/Hexo', {
            params: {
                type: 'like',
                title: this.post.title,
                url: (this.$store.state.global.hexoConfig.url.url.indexOf('/') == -1 ? this.$store.state.global.hexoConfig.url.url : this.$store.state.global.hexoConfig.url.url + '/') + this.post.slug
            }
        })
        this.likeNum = likeNum.data.data.like;

        await this.getCommentsLen();
    },
    mounted: function () {
        let that = this;
        let el = this.$el.querySelector(`.${that.$style.postCardTitle}`);

        let viewPortHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
        let offsetTop = el.offsetTop
        let scrollTop = document.documentElement.scrollTop
        let top = offsetTop - scrollTop
        if (top <= viewPortHeight + 50) {
            that.animationState = true;
        }

        window.addEventListener('scroll', () => {
            viewPortHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
            offsetTop = el.offsetTop
            scrollTop = document.documentElement.scrollTop
            top = offsetTop - scrollTop
            if (top <= viewPortHeight + 50) {
                that.animationState = true;
            }
        })
    },
    methods: {
        imageLoaded: function(type) {
            if (type == 'avatar') {
                this.avatarLoaded = true;
            } else if (type == 'cover') {              
                this.coverLoaded = true;
            }
        },
        getCommentsLen: async function (page = 1, pageSize = 1000) {
            let data;
            let commentLen = 0;
            const count = function (arr) {
                for (let i in arr) {
                    let item = arr[i];
                    commentLen++;
                    item.children && count(item.children)
                }
            };
            try {
                data = await this.$axios.get(process.env.VUE_APP_COMMENT_API + '/comments', {
                    params: {
                        page: page,
                        pageSize: pageSize,
                        articleId: this.articleId
                    }
                });
                data = data.data;
                if (data.status === 'success') {
                    count(data.data);
                    this.commentNum = commentLen;
                } else {
                    console.log(data);
                }
            } catch (e) {
                await this.$axios.get(process.env.VUE_APP_COMMENT_API + '/articles', {
                    params: {
                        title: this.title,
                        articleId: this.articleId,
                        url: (this.$store.state.global.hexoConfig.url.url.indexOf('/') == -1 ? this.$store.state.global.hexoConfig.url.url : this.$store.state.global.hexoConfig.url.url + '/') + this.slug
                    }
                });
                try {
                    data = await this.$axios.get(process.env.VUE_APP_COMMENT_API + '/comments', {
                        params: {
                            page: page,
                            pageSize: pageSize,
                            articleId: this.articleId
                        }
                    });
                    data = data.data;
                    if (data.status === 'success') {
                        count(data.data);
                        this.commentNum = commentLen;
                    } else {
                        console.log(data);
                    }
                } catch (e) {
                    console.log(e);
                }
            }
        },
    }
};